import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintPenerimaanSupplier = (
  row1isi = "",
  row2isi = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  // let data = [
  //   {
  //     kode_supplier: "SP001",
  //     nama_supplier: "SUPPLIER",
  //     detail: [
  //       {
  //         tanggal_invoice: "2021-03-01",
  //         no_bon: "CB44C4-F832-031A-3026-EB855A3D777F",
  //         detail_barang: [
  //           {
  //             no_urut: 1,
  //             kode_jenis: "990PER107",
  //             keterangan: "990 PER 107",
  //             kadar_cetak: 100,
  //             kadar_beli: 99,
  //             kode_barcode: "-",
  //             qty: 2,
  //             plastik: 2,
  //             kulit: 0,
  //             pkg: 5,
  //             gross: 10,
  //             bruto: 5,
  //             netto: 4.95,
  //             ongkos_rp: 100000,
  //           },
  //           {
  //             no_urut: 2,
  //             kode_jenis: "GLKEPRAS78",
  //             keterangan: "GLKEPRAS 78",
  //             kadar_cetak: 78,
  //             kadar_beli: 100,
  //             kode_barcode: "-",
  //             qty: 5,
  //             plastik: 5,
  //             kulit: 0,
  //             pkg: 5,
  //             gross: 60,
  //             bruto: 55,
  //             netto: 55,
  //             ongkos_rp: 100000,
  //           },
  //         ],
  //         total_bruto: 60,
  //         total_netto: 59.95,
  //         total_netto_ext: 59.95,
  //         total_rp: 200000,
  //       },
  //     ],
  //   },
  // ];
  doc.setFontSize(15);
  doc.text("LAPORAN DETAIL PENERIMAAN SUPPLIER", 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   row 2
  doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    doc.text(`Kode Supplier : ${item.kode_supplier}`, 14, 35);
    doc.text(`Nama Supplier : ${item.nama_supplier}`, 100, 35);
    item.detail.forEach((list) => {
      tableColumn = [
        [
          {
            content: `TANGGAL INVOICE : ${list.tanggal_invoice}`,
            colSpan: 12,
          },
        ],
        [
          {
            content: `NO BON : ${list.no_bon}`,
            colSpan: 12,
          },
        ],
        [
          {
            content: `NO`,
          },
          {
            content: `KODE JENIS`,
          },
          {
            content: `KETERANGAN`,
          },
          {
            content: `KADAR CETAK`,
          },
          {
            content: `KADAR BELI`,
          },
          {
            content: `QTY`,
          },
          {
            content: `P`,
          },
          {
            content: `K`,
          },
          {
            content: `PKG`,
          },
          {
            content: `GROSS`,
          },
          {
            content: `BRUTO`,
          },
          {
            content: `NETTO`,
          },
          {
            content: `ONGKOS`,
          },
        ],
      ];
      list.detail_barang.forEach((barang, index) => {
        let rows = [
          barang.no_urut,
          barang.kode_jenis,
          barang.keterangan,
          barang.kadar_cetak,
          barang.kadar_beli,
          barang.qty,
          barang.plastik,
          barang.kulit,
          barang.pkg,
          barang.gross,
          barang.bruto,
          barang.netto,
          parseFloat(barang.ongkos_rp).toLocaleString("id-ID"),
        ];
        tableRows.push(rows);
        console.log(tableRows);
      });
      let footer = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        item.total_bruto,
        item.total_netto,
        parseFloat(item.total_rp).toLocaleString("id-ID"),
      ];
      footRows.push(footer);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footRows,
      startY: index === 0 ? 40 : finalY + 5,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
    });
    finalY = doc.autoTableEndPosY() + 10;
    tableRows = [];
    footRows = [];
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  doc.text(`User	: ${username}`, 14, finalY + 10);
  doc.text(`Cetak	: ${tanggal}`, 14, finalY + 16);
  doc.text(`Valid	: ${validby}`, 14, finalY + 22);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPrintPenerimaanSupplier;
