import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanHutangSupplierSummary = (data) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  tableColumn = [
    [
      {
        content: `KODE SUPPLIER`,
      },
      {
        content: `NAMA SUPPLIER`,
      },
      {
        content: `AWAL NETTO`,
      },
      {
        content: `AWAL RP`,
      },
      {
        content: `IN NETTO`,
      },
      {
        content: `IN RP`,
      },
      {
        content: `OUT NETTO`,
      },
      {
        content: `OUT RP`,
      },
      {
        content: `AKHIR NETTO`,
      },
      {
        content: `AKHIR RP`,
      },
    ],
  ];

  doc.setFontSize(15);
  doc.text("LAPORAN SUMMARY HUTANG SUPPLIER", 14, 15);
  doc.setFontSize(10);

  data.forEach((barang, index) => {
    let rows = [
      barang.kode_supplier,
      barang.nama_supplier,
      barang.awal_netto,
      parseFloat(barang.awal_rp).toLocaleString("id-ID"),
      barang.in_netto,
      parseFloat(barang.in_rp).toLocaleString("id-ID"),
      barang.out_netto,
      parseFloat(barang.out_rp).toLocaleString("id-ID"),
      barang.akhir_netto,
      parseFloat(barang.akhir_rp).toLocaleString("id-ID"),
    ];
    tableRows.push(rows);
    footRows = [];
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: 30,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
  });
  finalY = doc.autoTableEndPosY() + 10;

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanHutangSupplierSummary;
