import Axios from "axios";
import React, { Component } from "react";
import { gagal } from "../Alert";
import { AxiosMasterGet } from "../Axios";
import { getToday } from "../GlobalComponent/function";
import { getUserData } from "../GlobalComponent/notification";
import FormSupplier from "./Component/formSupplier";
import LaporanHutangSupplier from "./Component/LaporanHutangSupplier";
import LaporanHutangSupplierCard from "./Component/LaporanHutangSupplierCard";
import LaporanHutangSupplierSummary from "./Component/LaporanHutangSupplierSummary";
import LaporanPrintPenerimaanSupplier from "./Component/LaporanPrintPenerimaanSupplier";
import LaporanPrintPenerimaanSupplierRekap from "./Component/LaporanPrintPenerimaanSupplierRekap";
import LaporanPrintReturnSupplier from "./Component/LaporanPrintReturnSupplier";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class LaporanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLaporan: [],
    };
  }

  handlePenerimaan(data) {
    AxiosMasterGet(
      "report/terima-barang-supplier/" +
        `${data.type}&${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
    )
      .then((res) =>
        this.setState({
          listLaporan: res && res.data,
        })
      )
      .then(() => {
        data.type === "DETAIL"
          ? LaporanPrintPenerimaanSupplier(
              data.tanggal_awal,
              data.tanggal_akhir,
              getUserData().user_name,
              getToday(),
              getUserData().user_name,
              this.state.listLaporan
            )
          : LaporanPrintPenerimaanSupplierRekap(
              data.tanggal_awal,
              data.tanggal_akhir,
              getUserData().user_name,
              getToday(),
              getUserData().user_name,
              this.state.listLaporan
            );
      })
      .catch((err) =>
        gagal(
          `Gagal Mengambil data, Mohon periksa koneksi anda, Detail :${err.response.data}`
        )
      );
  }
  handleHutang(data) {
    switch (data.type) {
      case "NORMAL":
        AxiosMasterGet("report/hutang-supplier")
          .then((res) =>
            this.setState({
              listLaporan: res && res.data,
            })
          )
          .then(() => LaporanHutangSupplier(this.state.listLaporan));
        break;
      case "CARD":
        AxiosMasterGet(
          "report/hutang-supplier-card/" +
            `${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) =>
            this.setState({
              listLaporan: res && res.data,
            })
          )
          .then(() => LaporanHutangSupplierCard(this.state.listLaporan));
        break;
      case "SUMMARY":
        AxiosMasterGet(
          "report/hutang-supplier-summary/" +
            `${data.kode_supplier}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) =>
            this.setState({
              listLaporan: res && res.data,
            })
          )
          .then(() => LaporanHutangSupplierSummary(this.state.listLaporan));
        break;
      default:
        break;
    }
  }
  handleKartuHutang(data) {}
  handleOmzet(data) {}
  handleReturn(data) {
    Axios.get(serverNonMaster + "spp/dc/rtr/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanReturnSupplier", JSON.stringify(res.data))
      )
      .catch((err) => gagal("Gagal Mengambil data, Mohon periksa koneksi anda"))
      .then(() =>
        LaporanPrintReturnSupplier(
          "Tanggal",
          `${data.tglFrom} - ${data.tglTo}`,
          "ADMIN",
          "2020-12-16",
          "ADMIN",
          "LAPORAN RETURN SUPPLIER"
        )
      );
  }
  handleTitip(data) {
    Axios.get(serverNonMaster + "spp/dc/ttp/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanTitipSupplier", JSON.stringify(res.data))
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleTolakan(data) {
    Axios.get(serverNonMaster + "spp/dc/tlkrtr/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem(
          "LaporanTolakReturSupplier",
          JSON.stringify(res.data)
        )
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleTransaksi(data) {
    Axios.get(serverNonMaster + "spp/dc/byr/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem(
          "LaporanTransaksiPembayaranSupplier",
          JSON.stringify(res.data)
        )
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#penerimaan-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Penerimaan</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#return-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link "
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Return</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#hutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Hutang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#kartu-hutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Kartu Hutang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#omzet-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Omzet</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#transaksi-pembayaran-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Transaksi Pembayaran
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#tolakan-return-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Tolakan Return</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#titip-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Titip</span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show active" id="penerimaan-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handlePenerimaan(data)}
                    />
                  </div>
                  <div className="tab-pane" id="return-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleReturn(data)}
                    />
                  </div>
                  <div className="tab-pane" id="hutang-tab">
                    <FormSupplier
                      from="hutang"
                      onSubmit={(data) => this.handleHutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="kartu-hutang-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleKartuHutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="omzet-tab">
                    <FormSupplier
                      from="omzet"
                      onSubmit={(data) => this.handleOmzet(data)}
                    />
                  </div>
                  <div className="tab-pane" id="transaksi-pembayaran-tab">
                    <FormSupplier
                      from="transaksi"
                      onSubmit={(data) => this.handleTransaksi(data)}
                    />
                  </div>
                  <div className="tab-pane" id="tolakan-return-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleTolakan(data)}
                    />
                  </div>
                  <div className="tab-pane" id="titip-tab">
                    <FormSupplier
                      from="penerimaan"
                      onSubmit={(data) => this.handleTitip(data)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LaporanSupplier;
