import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getsupplierList } from "../../actions/supplierAction";
import {
  ReanderField,
  ReanderSelect,
} from "../../GlobalComponent/notification";

class formSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kode_perhiasan: "",
      tipeOmzet: "PERTAHUN",
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  render() {
    switch (this.props.from) {
      case "penerimaan":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((list) => {
                    let data = {
                      value: list.kode_supplier,
                      name: list.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="type"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "DETAIL",
                      name: "DETAIL",
                    },
                    {
                      value: "REKAP",
                      name: "REKAP",
                    },
                  ]}
                  type="text"
                  label="Type Laporan"
                  placeholder="Masukan Type Laporan"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "hutang":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_supplier"
                  component={ReanderSelect}
                  options={this.props.listsupplier.map((hasil) => {
                    let data = {
                      value: hasil.kode_supplier,
                      name: hasil.nama_supplier,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Supplier"
                  placeholder="Masukan Kode Supplier"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="type"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "NORMAL",
                      name: "NORMAL",
                    },
                    {
                      value: "CARD",
                      name: "CARD",
                    },
                    {
                      value: "SUMMARY",
                      name: "SUMMARY",
                    },
                  ]}
                  type="text"
                  label="Type Laporan"
                  placeholder="Masukan Type Laporan"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Awal"
                  placeholder="Masukan Tanggal Awal"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Akhir"
                  placeholder="Masukan Tanggal Akhir"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );

      case "omzet":
        return (
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Tipe</label>
              <Field
                name="tipe"
                className="form-control"
                component="select"
                onChange={this.inputChange("tipeOmzet")}
              >
                <option value="PERTAHUN" key="PERTAHUN">
                  PERHATUN
                </option>
                <option value="PERBULAN" key="PERBULAN">
                  PERBULAN
                </option>
                <option value="PERTANGGAL" key="PERTANGGAL">
                  PERTANGGAL
                </option>
              </Field>
            </div>
            {this.state.tipeOmzet === "PERTAHUN" ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="tahunAwal"
                    label="Tahun Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="tahunAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            ) : this.state.tipeOmzet === "PERBULAN" ? (
              <>
                <div className="col-lg-4">
                  <Field
                    name="bulanAwal"
                    label="Bulan Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="bulanAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            ) : (
              <>
                <div className="col-lg-4">
                  <Field
                    name="dateAwal"
                    label="Tanggal Awal"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4">
                  <Field
                    name="dateAkhir"
                    label="Sampai Dengan"
                    className="form-control"
                    component={ReanderField}
                    type="date"
                  />
                </div>
              </>
            )}
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </div>
        );

      case "transaksi":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <label htmlFor="">Supplier</label>
                <Field
                  name="supplier"
                  label="Supplier"
                  className="form-control"
                  component="select"
                >
                  <option
                    value="DEFUALTSUPPLIER"
                    key="DEFUALTSUPPLIER"
                  ></option>
                </Field>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Jenis</label>
                <Field
                  name="jenis"
                  label="Jenis"
                  className="form-control"
                  component="select"
                >
                  <option value="SEMUA" key="123qwe">
                    SEMUA
                  </option>
                  <option value="CASH" key="CASH">
                    CASH
                  </option>
                  <option value="TRANSFER" key="TRANSFER">
                    TRANSFER
                  </option>
                  <option value="CIOK" key="CIOK">
                    CIOK
                  </option>
                  <option value="ROSOK" key="ROSOK">
                    ROSOK
                  </option>
                  <option value="PUTUSRP" key="PUTURP">
                    PUTUS RP
                  </option>
                  <option value="PUTUSGR" key="PUTUSGR">
                    PUTUS GR
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={ReanderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

formSupplier = reduxForm({
  form: "formSupplier",
  enableReinitialize: true,
})(formSupplier);
export default connect((state) => {
  return {
    listsupplier: state.provinsi.supplier,
  };
})(formSupplier);
